@import url('https://fonts.googleapis.com/css2?family=Inter:wght@400;600&display=swap');

*{
  margin: 0;
  padding: 0;
  --c1: #244d61;
  --c2: #5689c0;
  --c3: #75e2ff;
  --c4: #eaebed;
  --c5: #e65c4f;
  font-family: 'Inter', sans-serif;
}

body{
  background-color: var(--c1);
  color: var(--c4);
}

button{
  color: var(--c4);
  cursor: pointer;
}

.container{
  max-width: 1000px;
  min-height: 100vh;
  margin: auto;
}

.section{
  min-height: 90vh;
  display: flex;
  justify-content: center;
  align-items: center;
  /*background-color: #444;*/
}

.navbar{
  width: 100%;
  background-color: var(--c1);
} 

.nav-normal{
  width: 100%;
  display: flex;
  justify-content: space-between;
}

.nav-compact,
.nav-compact #menu{
  display: none;
}

.navbar button{
  background-color: transparent;
  border: none;
  padding: 1em 2em;
  border-bottom: 2px solid transparent;
  letter-spacing: .1em;
  font-size: .9em;
}

.navbar button:hover{
  border-bottom: 2px solid var(--c2);
}

.navbar button:active{
  border-bottom: 2px solid var(--c3);
  background-color: var(--c3);
  color: #223;
}

.navbar #configButton:hover,
.navbar #menuButton:hover{
  border-bottom: 2px solid var(--c5);
}

.navbar #configButton:active,
.navbar #menuButton:active{
  background-color: var(--c5);
  color: #223;
}

.configModal{
  display: none;
  position: absolute;
  z-index: 99;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background-color: var(--c1);
  box-shadow: 0 0 10px 1px rgba(0, 25, 50, .5);
}

.langOptions{
  display: flex;
  flex-direction: column;
  text-align: center;
  margin: 2em;
}

.langOptions h3{
  margin-bottom: 1em;
}

.configModal button{
  background-color: transparent;
  border: none;
  padding: 1em 2em;
  letter-spacing: .1em;
  font-size: .9em;
  margin: .5em;
}

.configModal button:hover{
  background-color: var(--c5);
  color: #223;
}

.title{
  font-size: 3.2rem;
  color: var(--c5);
  text-align: center;
  letter-spacing: .3rem;
}

.welcome-sec .title{
  font-size: 4.5rem;
  transform: translateY(-2rem);
}

.welcome-sec h2{
  text-align: center;
  transform: translateY(-2rem);
}

.spanNormal{
  color: var(--c4);
  font-size: 1.3rem;
  letter-spacing: 0;
}

.spanSpecial{
  color: var(--c2);
  font-size: 1.3rem;
  letter-spacing: 0;
}

.about-sec img{
  width: 2rem;
  margin: 1rem .7rem 0 0;
}

.about-sec{
  line-height: 1.9rem;
}

.about-sec,
.learning-sec{
  display: grid;
  grid-template-columns: 1fr 1fr;
  place-items: center;
}

.learningGrid{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  place-items: center;
  gap: 2rem;
  max-width: 400px;
}

.learningGrid div{
  width: 6rem;
  height: 6rem;
  box-shadow: 0 0 5px 2px rgba(0, 25, 50, .5);
  display: grid;
  place-items: center;
  border-radius: 4px;
  background-color: var(--c2);
}

.learningGrid img{
  width: 80%;
}

.learning-sec h2{
  font-size: 1.2rem;
  color: var(--c4);
  text-align: center;
  margin-top: 1.5rem;
}

.projects-sec h1{
  margin-bottom: 4rem;
}

.projectsGrid{
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  place-items: center;
  gap: 3rem;
}

.projectsGrid div{
  width: 300px;
}

.projectsGrid img{
  width: 100%;
  border-radius: 4px;
}

@media(max-width: 1050px){
  .container{
    max-width: 95%;
  }
  .projectsGrid{
    grid-template-columns: 1fr 1fr;
    gap: 1.5rem;
  }
  .projects-sec h1{
    margin: 1rem 0 1.6rem 0;
  }
}

@media(max-width: 800px){
  .learningGrid{
    gap: 1rem;
  }
}

@media(max-width: 700px){
  .nav-normal{
    display: none;
  }
  .nav-compact{
    display: block;
  }
  .nav-compact #nav{
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .nav-compact #menu{
    justify-content: space-between;
    border-bottom: 2px solid rgba(0, 25, 50, .5);
    position: absolute;
    width: 95%;
    z-index: 99;
    background-color: var(--c1);
  }
  #menu button{
    padding: 1rem;
  }
  #menuButton:focus{
    background-color: var(--c5);
    color: #223;
  }
  .about-sec,
  .learning-sec{
    grid-template-columns: 1fr ;
    gap: 3rem;
  }
  .about-sec div{
    text-align: center;
  }
  .projects-sec{
    margin-bottom: 2rem;
  }
  .projectsGrid{
    grid-template-columns: 1fr ;
    gap: 1.5rem;
  }
}

@media(max-width: 600px){
  .nav-compact #menu{
    flex-direction: column;
  }
}